"use client";
import { useState, useEffect } from 'react';
import { auth, googleProvider, db } from '../firebase';
import { signInWithPopup, signOut, onAuthStateChanged, createUserWithEmailAndPassword, signInWithEmailAndPassword, sendPasswordResetEmail, updateProfile } from 'firebase/auth';
import { doc, setDoc, getDoc } from 'firebase/firestore';
import Link from 'next/link';
import { FaGoogle, FaUserGraduate, FaTimes, FaHeadset, FaUserCircle, FaShieldAlt } from 'react-icons/fa';

export default function Navbar() {
  const [user, setUser] = useState(null);
  const [userData, setUserData] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [authMode, setAuthMode] = useState('login'); 
  
  // Form States
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [msg, setMsg] = useState('');

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (u) => {
      setUser(u);
      if (u) {
        setShowModal(false);
        const docSnap = await getDoc(doc(db, "users", u.uid));
        if (docSnap.exists()) setUserData(docSnap.data());
      } else {
        setUserData(null);
      }
    });
    return () => unsubscribe();
  }, []);

  const handleGoogle = async () => {
    try { 
      const res = await signInWithPopup(auth, googleProvider);
      const u = res.user;
      const userRef = doc(db, "users", u.uid);
      const userSnap = await getDoc(userRef);
      if (!userSnap.exists()) {
        await setDoc(userRef, {
          name: u.displayName, email: u.email, photo: u.photoURL,
          phone: "", course: "Not Allotted", role: "student", // Default role
          createdAt: new Date().toISOString()
        });
      }
    } catch (e) { setError(e.message); }
  };

  const handleAuth = async (e) => {
    e.preventDefault();
    setError('');
    try {
      if (authMode === 'signup') {
        const res = await createUserWithEmailAndPassword(auth, email, password);
        await updateProfile(res.user, { displayName: name });
        await setDoc(doc(db, "users", res.user.uid), {
          name: name, email: email, phone: phone, photo: "",
          course: "Not Allotted", role: "student",
          createdAt: new Date().toISOString()
        });
      } else if (authMode === 'login') {
        await signInWithEmailAndPassword(auth, email, password);
      } else if (authMode === 'forgot') {
        await sendPasswordResetEmail(auth, email);
        setMsg('Reset link sent!');
      }
    } catch (err) { setError(err.message.replace("Firebase:", "")); }
  };

  // CHECK IF USER HAS ADMIN ACCESS
  const isAdmin = userData?.role === 'Full Control' || userData?.role === 'Manager' || userData?.role === 'Staff' || userData?.role === 'Viewer' || user?.email === 'contact@kamarjahan.in';

  return (
    <>
      <nav className="bg-white/90 backdrop-blur border-b sticky top-0 z-50 h-16 px-4 md:px-8 flex items-center justify-between shadow-sm">
        <Link href="/" className="font-bold text-xl tracking-tight text-slate-900">VIRULENCE <span className="text-teal-600">EDU</span></Link>
        
        <div className="flex items-center gap-4 text-sm font-medium">
          <Link href="/" className="hidden md:block hover:text-teal-600">Home</Link>
          <Link href="/#courses" className="hidden md:block hover:text-teal-600">Courses</Link>
          
          {user && <Link href="/tickets" className="flex items-center gap-1 text-teal-600"><FaHeadset /> Support</Link>}

          {/* DYNAMIC ADMIN BUTTON */}
          {isAdmin && (
            <Link href="/admin" className="flex items-center gap-1 text-red-600 border border-red-200 px-2 py-1 rounded text-xs font-bold uppercase hover:bg-red-50">
               <FaShieldAlt/> {userData?.role || 'Admin'}
            </Link>
          )}

          {user ? (
            <div className="flex items-center gap-3 pl-3 border-l ml-3">
               <Link href="/profile">
                 {userData?.photo ? <img src={userData.photo} className="w-9 h-9 rounded-full border object-cover" /> : <FaUserCircle className="text-3xl text-slate-400"/>}
               </Link>
            </div>
          ) : (
            <button onClick={() => { setShowModal(true); setAuthMode('login'); }} className="bg-slate-900 text-white px-4 py-2 rounded-full font-bold text-xs flex items-center gap-2">
              <FaUserGraduate /> Login
            </button>
          )}
        </div>
      </nav>
      {/* (Modal code remains same as previous steps) */}
      {showModal && (
        <div className="fixed inset-0 z-50 bg-black/60 flex items-center justify-center p-4 backdrop-blur-sm">
          <div className="bg-white rounded-xl shadow-2xl w-full max-w-sm relative overflow-hidden">
            <button onClick={() => setShowModal(false)} className="absolute top-3 right-3 text-slate-400"><FaTimes /></button>
            <div className="p-8">
               {/* Same form code as previous version */}
               <h2 className="text-xl font-bold mb-4 capitalize">{authMode}</h2>
               {error && <p className="text-red-500 text-xs mb-2">{error}</p>}
               {msg && <p className="text-green-500 text-xs mb-2">{msg}</p>}
               {authMode !== 'forgot' && <button onClick={handleGoogle} className="w-full border p-2 mb-4 rounded text-sm font-bold flex justify-center gap-2"><FaGoogle className="text-red-500"/> Google</button>}
               <form onSubmit={handleAuth} className="space-y-3">
                 {authMode === 'signup' && <><input placeholder="Name" value={name} onChange={e=>setName(e.target.value)} className="w-full border p-2 rounded"/><input placeholder="Phone" value={phone} onChange={e=>setPhone(e.target.value)} className="w-full border p-2 rounded"/></>}
                 <input placeholder="Email" value={email} onChange={e=>setEmail(e.target.value)} className="w-full border p-2 rounded"/>
                 {authMode !== 'forgot' && <input type="password" placeholder="Password" value={password} onChange={e=>setPassword(e.target.value)} className="w-full border p-2 rounded"/>}
                 <button className="w-full bg-slate-900 text-white py-2 rounded font-bold capitalize">{authMode}</button>
               </form>
            </div>
          </div>
        </div>
      )}
    </>
  );
}